$day-cell-heiht: 100px;
$day-cell-width: 100px;
$cells-gap: 1px;
$calendar-vertical-padding: 10px;
$month-length: 31;
$calendar-width: ($day-cell-width *7) + ($cells-gap * 6) 
  + ($calendar-vertical-padding * 2);

$color-text: #000;
$color-cell-border: #000;
$color-cell-bcg: #eee;
$color-hover-bcg: #ffc0cb;

$days-shift:
  "mon" startShift(0),
  "tue" startShift(1),
  "wed" startShift(2),
  "thu" startShift(3),
  "fri" startShift(4),
  "sat" startShift(5),
  "sun" startShift(6);
